import classNames from 'classnames';

export enum ButtonType {
  BUTTON = 'button',
  SUBMIT = 'submit',
  RESET = 'reset',
}

export interface ButtonProps {
  children: React.ReactNode;
  className?: string;
  type?: ButtonType;
  disabled?: boolean;
  onClick?: () => void;
}

export function Button(props: ButtonProps) {
  const _props = { ...props };
  _props.className = classNames('rounded font-bold', props.className);
  return (
    <button
      className={_props.className}
      type={props.type ?? ButtonType.BUTTON}
      disabled={props.disabled ?? false}
      onClick={props.onClick}
    >
      {props.children}
    </button>
  );
}

const large = 'h-12 px-4';
const medim = 'h-10 px-6';
const small = 'h-[1.875rem] px-4';

const primary =
  'text-base text-white hover:text-white active:text-white bg-gradient-to-r from-[#e921c3] to-[#0067ff] hover:from-[#ef64d5] hover:to-[#4d94ff] active:from-[#c31ba3] active:to-[#0056d6] disabled:opacity-40 disabled:hover:text-white disabled:hover:from-[#e921c3] disabled:hover:to-[#0067ff]';

export function ButtonPrimaryLarge(props: ButtonProps) {
  const _props = { ...props };
  _props.className = classNames(large, primary, props.className);
  return <Button {..._props} />;
}

export function ButtonPrimaryMedim(props: ButtonProps) {
  const _props = { ...props };
  _props.className = classNames(medim, primary, props.className);
  return <Button {..._props} />;
}

export function ButtonPrimarySmall(props: ButtonProps) {
  const _props = { ...props };
  _props.className = classNames(small, primary, props.className);
  return <Button {..._props} />;
}

export function ButtonOutlineLarge(props: ButtonProps) {
  const _props = { ...props };
  _props.className = classNames(large, props.className);
  return <Button {..._props} />;
}

export function ButtonOutlineMedim(props: ButtonProps) {
  const _props = { ...props };
  _props.className = classNames(medim, props.className);
  return <Button {..._props} />;
}

export function ButtonOutlineSmall(props: ButtonProps) {
  const _props = { ...props };
  _props.className = classNames(small, props.className);
  return <Button {..._props} />;
}
