import { useRef, useState, Fragment, useEffect } from 'react';
import { Dialog, Transition } from '@headlessui/react';
import {
  // useNavigate,
  useLocation,
} from 'react-router-dom';
import classNames from 'classnames';

import { ButtonPrimaryLarge } from '@/components/Button';

import menu from '@/assets/common/menu.png';
import logo from '@/assets/logo.png';
import coming from '@/assets/common/coming.png';

import style from './index.module.css';

function Header() {
  const navs = [
    { name: 'Doc', link: 'https://docs.coslend.com/', forbidden: false },
    { name: 'Pools', link: 'pools', forbidden: true },
    { name: 'Markets', link: 'markets', forbidden: true },
  ];
  const [isOpen, setIsOpen] = useState(false);

  const visibleRef = useRef(false);
  const [visible, setVisible] = useState(false);

  const handleClick = () => {
    visibleRef.current = !visibleRef.current;
    setVisible(visibleRef.current);
  };

  const closeModal = () => {
    setIsOpen(false);
  };

  useEffect(() => {
    if (isOpen) {
      setTimeout(() => {
        setIsOpen(false);
      }, 2000);
    }
  });

  // const navigate = useNavigate();
  const location = useLocation();

  const isHome = location.pathname === '/';

  const started = () => {
    // if (isHome) {
    //   navigate('pools');
    // }
    setIsOpen(true);
  };

  return (
    <header className="z-50 text-base font-bold md:px-8 md:py-6">
      <div className="relative mx-auto max-w-[70.5rem]">
        <div className="flex flex-wrap items-center justify-between">
          <div className="flex h-20 items-center md:h-12">
            <img className="mx-4 h-6 md:hidden" src={menu} alt="menu" onClick={handleClick} />
            <img className="h-10" src={logo} alt="logo" />
          </div>
          <div
            className={classNames(
              visible
                ? 'h-auto max-h-96 py-10 transition-all'
                : '-mb-px h-0 overflow-hidden border-t-0 py-0 transition-all',
              'flex flex-auto flex-wrap items-center justify-end gap-y-6 bg-[#1f1f1f] md:mr-[9.875rem] md:h-auto md:space-x-6 md:border-none md:bg-transparent md:p-0 md:transition-none'
            )}
          >
            {navs.map((nav, index) => {
              return (
                <div
                  className={classNames(
                    !nav.forbidden ? 'text-white ' : 'text-white/50',
                    'w-screen cursor-pointer px-4 hover:text-white md:w-auto md:px-3'
                  )}
                  key={index}
                  onClick={() => {
                    return nav.forbidden ? setIsOpen(true) : window.open(nav.link);
                  }}
                >
                  {nav.name}
                </div>
              );
            })}
          </div>
          <ButtonPrimaryLarge className="absolute top-4 right-4 md:top-0 md:right-0" onClick={started}>
            {isHome ? 'Get started' : 'Connect Wallet'}
          </ButtonPrimaryLarge>
        </div>
      </div>
      <Transition appear show={isOpen} as={Fragment}>
        <Dialog as="div" className="fixed inset-0 z-10 overflow-y-auto" onClose={closeModal}>
          <div className="min-h-screen px-4 text-center">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0"
              enterTo="opacity-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100"
              leaveTo="opacity-0"
            >
              <Dialog.Overlay className="fixed inset-0" />
            </Transition.Child>

            {/* This element is to trick the browser into centering the modal contents. */}
            <span className="inline-block h-screen align-middle" aria-hidden="true">
              &#8203;
            </span>
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <div
                className={classNames(
                  style.toast,
                  'z-50 my-8 mt-[0] inline-block w-full max-w-md transform overflow-hidden bg-white p-[1rem] text-left align-top shadow-xl transition-all'
                )}
              >
                <Dialog.Title as="h3" className="flex items-center text-lg font-medium leading-6 text-[#fff]">
                  <img className={style.coming} src={coming} alt="" />
                  Coming Soon!
                </Dialog.Title>
                {/* <div className="mt-4">
                  <button
                    type="button"
                    className="inline-flex justify-center rounded-md border border-transparent bg-blue-100 px-4 py-2 text-sm font-medium text-blue-900 hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500 focus-visible:ring-offset-2"
                    onClick={closeModal}
                  >
                    Got it!
                  </button>
                </div> */}
              </div>
            </Transition.Child>
          </div>
        </Dialog>
      </Transition>
    </header>
  );
}

export default Header;
